import React from 'react';
import APILayout from '../../../../layouts/APILayout';
import Section from '../../../../modules/apidoc/components/Section';
import FunctionName from '../../../../modules/apidoc/components/FunctionName';
import ParameterTable from '../../../../modules/apidoc/components/ParameterTable';
import Parameter from '../../../../modules/apidoc/components/Parameter';
import { MapType, DrawEventType, ChangeEventType } from '../../../../modules/apidoc/components/OpenlayersDataTypes';
import ParameterGroup from '../../../../modules/apidoc/components/ParameterGroup';
import Highlighted from '../../../../modules/apidoc/components/Highlighted';
import { InfoAlert } from '../../../../modules/apidoc/components/Alerts';
import FunctionDescription from '../../../../modules/apidoc/components/FunctionDescription';
import Returns from '../../../../modules/apidoc/components/Returns';
import Link from '../../../../modules/apidoc/components/Link';

const OpenlayersDrawInteractionDocLink = ({ children }) => <Link href="https://openlayers.org/en/latest/apidoc/module-ol_interaction_Draw-Draw.html">{children || 'https://openlayers.org/en/latest/apidoc/module-ol_interaction_Draw-Draw.html'}</Link>;

export default () => (
    <APILayout>
        <h4 className='font-weight-bold mb-4'>easyolmaps/vector/Drawing</h4>
        <Section>
            <FunctionName parameters={['map', 'options']}>new Drawing</FunctionName>
            <ParameterTable>
                <Parameter name='map' type={<MapType />}>The map where to do the drawing</Parameter>
                <ParameterGroup name='options' type='Drawing Options'>
                    <ParameterTable name='options'>
                        <Parameter name='shape' type='string' >Shape to draw. It can be one of the following: <Highlighted>Point</Highlighted>, <Highlighted>Polygon</Highlighted>, <Highlighted>Line</Highlighted>, <Highlighted>Section</Highlighted>, <Highlighted>Box</Highlighted>, <Highlighted>Circle</Highlighted>.</Parameter>
                    </ParameterTable>
                    <InfoAlert>You can add other options of the <strong><OpenlayersDrawInteractionDocLink>openlayers's Draw Interaction</OpenlayersDrawInteractionDocLink></strong>.</InfoAlert>
                </ParameterGroup>
            </ParameterTable>
            <strong>Fires:</strong>
            <ul>
                <li><Highlighted><Link href="#">drawstart</Link></Highlighted>(<DrawEventType/>) - Triggerd when drawing starts.</li>
                <li><Highlighted><Link href="#">drawend</Link></Highlighted>(<DrawEventType/>) - Triggered when drawing finishes.</li>
                <li><Highlighted><Link href="#">drawing</Link></Highlighted>(<ChangeEventType/>) - Generic change event.</li>
            </ul>
        </Section>
        <Section>
            <FunctionName>start</FunctionName>
            <FunctionDescription>Start drawing.</FunctionDescription>
        </Section>
        <Section>
            <FunctionName>finish</FunctionName>
            <FunctionDescription>Finish drawing.</FunctionDescription>
        </Section>
        <Section>
            <FunctionName parameters={['type', 'listener']}>on</FunctionName>
            <ParameterTable>
                <Parameter name='type' type='string'>The event type.</Parameter>
                <Parameter name='listener' type='function'>The listener function.</Parameter>
                <Returns>this</Returns>
            </ParameterTable>
        </Section>
    </APILayout>
)